<template>
  <div class="actionSheet">
    <transition name="delay">
      <div class="mask" v-if="showAction" @click="clickMask"></div>
    </transition>
    <transition name="fade">
      <div class="sheet_column"  v-if="showAction" :style="{height: setHeight/37.5 + 'rem'}">
        <slot name="main"></slot>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
// import { Button } from 'mint-ui'

export default {
  data () {
    return {
      flag: true, // 判断可否点击遮罩层
      timer: null // 定时器
    }
  },

  mounted () {},

  props: [ 'setHeight' ],

  component: {
    // Button
  },

  computed: {
    ...mapGetters(['getActionsheet', 'showAction']),
    actionSheet () {
      return this.getActionsheet
    }
  },
  methods: {
    ...mapActions([
      'showActionsheet'
    ]),
    clickMask () {
      // 若弹窗隐藏，在300ms内禁止点击
      if (this.flag) {
        this.showActionsheet()
      }
      this.flag = false
      window.clearTimeout(this.timer)
      this.timer = window.setTimeout(() => {
        this.flag = true
        window.clearTimeout(this.timer)
      }, 500)
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../styles/mixin.less';

.actionSheet{
  color: #717171;
  .mask{
    .fs;
    z-index: 998;
    background: rgba(0, 0, 0, .5);
  }
  .sheet_column{
    .wh(100%, 300px);
    background: #FFF;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 999;
    border-radius: 6px 6px 0 0;
  }
}

/* 开始过渡阶段,动画出去阶段 */
.delay-leave-active{
  transition: translateY 0s ease-in .3s;
}
.fade-enter-active,.fade-leave-active{
  transition: all .3s ease-out 0s;
}
/* 进入开始 */
.fade-enter{
  transform: translateY(100%);
}
/* 出去终点 */
.fade-leave-to{
  transform: translateY(200%);
  opacity: 0;
}

</style>



