<template>
  <div id="currency">
    <head-vue title="意见反馈" goBack="true"></head-vue>
    <div class="conntent">
      <div class="tit3">
        选择反馈类型
      </div>
      <ul class="suggType">
        <li :class="{active: item.active} " :key="item.id"
            v-for=" item in suggestType"
            @click="tabType(item)">
            {{item.type}}
        </li>
      </ul>
      <div class="text">
        <textarea
          placeholder="你的建议将助我们更快成长"
          maxlength="100"
          @input="cacla"
          v-model="value"></textarea>
        <span>{{textlength}}/100</span>
      </div>
      <ul class="picture" ref="picture">
        <li @click="updatePicture(0)">
          <img src="../../assets/me/picture@3x.png" alt="">
        </li>
        <li @click="updatePicture(1)">
          <img src="../../assets/me/picture@3x.png" alt="">
        </li>
        <li @click="updatePicture(2)">
          <img src="../../assets/me/picture@3x.png" alt="">
        </li>
        <li @click="updatePicture(3)">
          <img src="../../assets/me/picture@3x.png" alt="">
        </li>
      </ul>
    </div>
    <!-- 修改头像途径 -->
    <actionSheet v-show="1" setHeight="200">
      <div slot="main">
        <ul class="changePic">
          <li>Change pic</li>
          <li @click="actionMode(1)">Take a photo</li>
          <li @click="actionMode(2)">Select from the phone album</li>
          <li @click="actionMode(3)">Cancel</li>
        </ul>
      </div>
    </actionSheet>
    <loading></loading>
    <div class="button">提交</div>
  </div>
</template>
<script>
import head from '../../components/common/head'
import actionSheet from '../../components/common/actionSheet'
import loading from '../../components/Loading/index'
import {
  localPhotoRequest,
  takePhotoRequest
} from '../../api/method'
import {
  modifyAvatarSendedResponse,
  modifyUserInformationResponse
} from '../../api/getData'
import { mapActions, mapGetters } from 'vuex'
export default {
  data () {
    return {
      textlength: 0,
      value: null,
      picIndex: 0, // 上传图片顺序(成功)
      clickIndex: 0, // 点击图片下标
      suggestType: [
        {
          type: '功能',
          active: true
        },
        {
          type: '建议',
          active: false
        },
        {
          type: 'bug',
          active: false
        },
        {
          type: '其它',
          active: false
        }
      ],
      picArr: []
    }
  },
  components: {
    'head-vue': head,
    actionSheet,
    loading
  },
  computed: {
    ...mapGetters(['getActionsheet']),
    actionSheet () {
      return this.getActionsheet
    }
  },
  created () {
  },
  mounted () {
    modifyAvatarSendedResponse(this.NAVIGATOR, this)
    modifyUserInformationResponse(this.NAVIGATOR, this, 'suggest')
  },
  methods: {
    ...mapActions(['setCurrency', 'showActionsheet']),
    cacla () {
      this.textlength = this.value.length
    },
    tabType (item) {
      this.suggestType.forEach(ele => {
        ele.active = false
      })
      item.active = true
    },
    updatePicture (num) {
      this.clickIndex = num
      this.showActionsheet()
    },
    actionMode (num) {
      switch (num) {
        case 1:
          takePhotoRequest(this.NAVIGATOR, this)
          this.showActionsheet()
          break
        case 2:
          localPhotoRequest(this.NAVIGATOR, this)
          if (this.$root.ISDEBUG) {
            window.modifyUserInformationResponse(true, '{"headPic":"http://file.startai.com.cn:3108/group1/M00/00/00/wKgQzVwfSUaEQlVAAAAAAAAAAAA897.jpg"}')
          }
          this.showActionsheet()
          break
        case 3:
          this.showActionsheet()
          break
        default:
          break
      }
    }
  }
}
</script>
<style lang="less" scoped>
#currency{
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  font-size: 30px;
  color: #222222;
  .conntent{
    padding: 40px 24px 0 24px;
    display: flex;
    flex-direction: column;
    .tit3{
      align-self: flex-start;
    }
    .suggType{
      margin-top: 44px;
      display: flex;
      li{
        margin-right: 34px;
        .wh-l(124px, 62px);
        border:  1px solid #595959;
      }
      .active{
        color: @themeColor;
        border-color: @themeColor;
      }
    }
    .text{
      .wh(98%, 300px);
      border: 1px solid #b1afaf;
      margin-top: 34px;
      border-radius:10px;
      display: flex;
      flex-direction: column;
      padding: 20px;
      span{
        align-self: flex-end;
      }
      textarea{
        flex: 1;
        outline: none;
        border: 0;
      }
    }
    .picture{
      display: flex;
      margin-top: 34px;
      li{
        .wh(144px, 144px);
        margin-right: 41px;
        img{
          .wh(100%, 100%)
        }
      }
      li:last-child{
        margin-right: 0;
      }
    }
  }
  .button{
    margin-top: 52px;
    .wh(653px, 98px);
    font-size: 36px;
  }
  .changePic{
    .wh(100%,auto);
    li{
      .wh-l(100%,100px);
      font-size: 31px;
      color: #575757;
      border-bottom: .5px solid #eeeeee;
    }
    li:nth-child(4){
        border-bottom: 0;
    }
    li:nth-child(1) {
      font-weight: 900;
    }
  }
}
</style>



